.App {
  margin: 0;
  padding: 0;
  background-size: 90% ;
  background-position: center;
  background-repeat: no-repeat;
  height: 100vh;
  display: flex;
  width: 100%;
  height: 100%;
  position: absolute;
 flex-direction: column;
 justify-content: flex-start;
}

.title-container{
  text-align: center;
  width: 100%;
  height:10%;
  background-color: #A020F0
}

.left-bottom-container{
  position: fixed;
  left:0;
  bottom: 0%;
  padding: 9.5%;
  top:4%;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.robot-icon{
  z-index: 999;
  position: fixed;
  bottom: 22px;
  right: 16px;
}
.df-messenger {
  z-index: 999;
  position: fixed;
  bottom: 16px;
  right: 16px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
